.bio-panel,
.athlete-panel,
.developer-panel {
  position: relative;
  z-index: 1;
  overflow-x: hidden;
  overflow-y: hidden;
  transition: 0.3s;
  height: 50%;
  font-family: 'Hina Mincho', serif;
}

.bio-box:hover .bio-img,
.athlete-box:hover .podium-img,
.developer-box:hover .developer-img {
  cursor: pointer;
  opacity: 0.4;
}

.bio-box:hover .bio-panel,
.athlete-box:hover .athlete-panel,
.developer-box:hover .developer-panel {
  color: white;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.home-text {
  font-family: 'Allison', cursive;
}

#about h2 {
  font-family: 'Rock Salt', cursive;
}

