body {
  overflow-x: hidden;
}

.tab {
  position: absolute;
  left: -100px;
  transition: 0.3s;
  padding: 12px;
  font-size: 25px;
  font-weight: bold;
  border-radius: 0 5px 5px 0;
  font-family: 'Amatic SC', cursive;
}

.tab:hover {
  left: 0;
  cursor: pointer;
}

.right-tab {
  position: absolute;
  right: -145px;
  transition: 0.3s;
  padding: 12px;
  font-size: 25px;
  font-weight: bold;
  border-radius: 5px 0 0 5px;
  font-family: 'Amatic SC', cursive;
}

.right-tab:hover {
  right: 0;
  cursor: pointer;
}

